import { Component } from '@angular/core';
import { Storage } from '@ionic/storage';
import { InAppBrowser, InAppBrowserOptions  } from '@ionic-native/in-app-browser';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

const biblioteca = 'http://www.biblioteca.unifesp.br/biblioteca/index.php';
const saldoRU = 'https://phpu.unifesp.br/ru_consulta/index.php';
const cardapio = 'www.unifesp.br/campus/sjc/servicosnae/restaurante/1647-cardapio-semanal-do-ru.html';
const email = 'https://www.email.unifesp.br';
const atestado = 'https://intranet.unifesp.br/restrict/index3.php';
const historico = 'https://intranet.unifesp.br/restrict/index3.php';

@IonicPage()
@Component({
  selector: 'page-utilidades',
  templateUrl: 'utilidades.html',
})
export class UtilidadesPage {

  options : InAppBrowserOptions = {
    location : 'yes',//Or 'no' 
    hidden : 'no', //Or  'yes'
    clearcache : 'yes',
    clearsessioncache : 'yes',
    zoom : 'yes',//Android only ,shows browser zoom controls 
    hardwareback : 'yes',
    mediaPlaybackRequiresUserAction : 'no',
    shouldPauseOnSuspend : 'no', //Android only 
    closebuttoncaption : 'Close', //iOS only
    disallowoverscroll : 'no', //iOS only 
    toolbar : 'yes', //iOS only 
    enableViewportScale : 'no', //iOS only 
    allowInlineMediaPlayback : 'no',//iOS only 
    presentationstyle : 'pagesheet',//iOS only 
    fullscreen : 'yes',//Windows only    
};

  constructor(public navCtrl: NavController, public navParams: NavParams, private iab: InAppBrowser, public storage: Storage) {
  }

  checkSession() {
    /*this.storage.get("aluno_nome").then((usu) => {
      if(usu == null) {
        this.navCtrl.push(LoginPage);
      }
    })*/
  }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad UtilidadesPage');
    document.getElementById("tabs").style.display = "none"
    document.getElementById("botao_menu").style.display = "none"
  }

  clickBiblioteca() {
    const browser = this.iab.create(biblioteca,'_system',this.options);
    browser.show();
  }



  clicksSaldo() {
    const browser = this.iab.create(saldoRU,'_system',this.options);
    browser.show();
  }



  clickCardapio() {
    const browser = this.iab.create(cardapio,'_system',this.options);
    browser.show();
  }



  clickEmail() {
    const browser = this.iab.create(email,'_system',this.options);
    browser.show();
  }

  clickAtestado() {
    const browser = this.iab.create(atestado,'_system',this.options);
    browser.show();
  }

 clickHistorico() {
    const browser = this.iab.create(historico,'_system',this.options);
    browser.show();
  }

}


